import React from 'react'
import { inject, observer } from 'mobx-react';
import { Button, Badge, Icon, DatePicker, Card, Timeline, Empty } from 'antd';
import moment from 'moment';
import './index.less'
import PieChart from './PieChart'

@inject('StatisticsStore')
@observer
class StatisticsManage extends React.Component {
  constructor(props) {
    super(props)
    this.state = {}
  }
  render() {
    const { detailInfo, searchDate } = this.props.StatisticsStore
    const { RangePicker } = DatePicker;
    return <div className='u-content'>
      <Card title="设备故障分析"  >
        <div className='UserTitle'>
          <div className='u-title-left'>
            <Button style={{ marginRight: 10 }} onClick={e => this.props.history.goBack()}><Icon type='left' /></Button>
           {/* 日期：<RangePicker onChange={this.changeDate} value={[searchDate[0] ? moment(searchDate[0]) : "", searchDate[1] ? moment(searchDate[1]) : ""]} placeholder={['开始时间', '结束时间']} />
            <Button type='primary' style={{ margin: 10 }}>
              查询
  </Button>*/}
            <div className='u-userName'>{detailInfo.deviceName}</div>
            <div className='u-userName'>{detailInfo.deviceStatus ? <Badge status="success" text="正常" />
              :
              <Badge status="error" text="故障" />}</div>
          </div>
        </div>
        <div className='userDetail'>
          <div className='u-detail-left'>
            <div className='u-tips-content'>
              <div className='u-tips'>
                <div class='u-tips-title'>设备编号</div>
                <div>{detailInfo.deviceCode}</div>
              </div>
              <div className='u-tips'>
                <div class='u-tips-title'>设备型号</div>
                <div>{detailInfo.deviceModelName}</div>
              </div>
              <div className='u-tips'>
                <div class='u-tips-title'>设备类型</div>
                <div>{detailInfo.deviceTypeName}</div>
              </div>
              <div className='u-tips'>
                <div class='u-tips-title'>合作伙伴编码</div>
                <div>{detailInfo.customerCode}</div>
              </div>
              <div className='u-tips'>
                <div class='u-tips-title'>合作伙伴名称</div>
                <div>{detailInfo.customerName}</div>
              </div>
              <div className='u-tips'>
                <div class='u-tips-title'>维修次数</div>
                <div>{detailInfo.maintainCount}</div>
              </div>
            </div>
          </div>
        </div>
        <div className='UserTitle'>
          <div style={{ flex: 1, border: '1px solid #eee', borderRadius: '4px', marginRight: 10 }}>
            {detailInfo.exceptionMap ? <PieChart /> : <Empty image={Empty.PRESENTED_IMAGE_SIMPLE} />}
          </div>
          <div className='u-log-content' style={{ flex: 1, border: '1px solid #eee', borderRadius: '4px', marginLeft: 10, marginTop: 0, padding: 20 }}>
            <p style={{ fontWeight: 600, fontSize: 20 }}>故障日志：</p>
            <Timeline>
              {detailInfo.workVOList ? detailInfo.workVOList.map((item, index) => (
                <Timeline.Item key={index}>
                  <div>
                    <div>故障描述：{item.description}</div>
                    <div>报修人：{item.applyName}</div>
                    <div>故障上报时间：{item.createTime}</div>
                    <div>维修人：{item.workerName}</div>
                    <div>工号：{item.workNo}</div>
                    <div>故障解决时间：{item.expecteTime}</div>
                  </div>
                </Timeline.Item>
              )) : <Empty image={Empty.PRESENTED_IMAGE_SIMPLE} />}
            </Timeline>
          </div>
        </div>
      </Card>
    </div>;
  }
  changeDate = (e, formate) => {
    this.props.StatisticsStore.searchDate = formate
  }
  handelClick = (e) => {
    console.log(e)
    this.props.StatisticsStore.columnsType = e.id
  }
}
export default StatisticsManage;