import React from 'react';
import { inject, observer } from 'mobx-react';
import {
    Chart,
    Geom,
    Axis,
    Tooltip,
    Coord,
    Label,
    Legend,
} from "bizcharts";
import DataSet from "@antv/data-set";

@inject('StatisticsStore')
@observer
class PieChart extends React.Component {
    formateData = () => {
        const { detailInfo } = this.props.StatisticsStore;
        const data = []
        for (let key in detailInfo.exceptionMap) {
            let count = +Number(detailInfo.exceptionMap[key] / detailInfo.maintainCount * 100).toFixed(2)
            data.push({ item: key, count: count })
        }
        return data
    }
    render() {
        const { DataView } = DataSet;

        const data = this.formateData()
        const dv = new DataView();
        dv.source(data).transform({
            type: "percent",
            field: "count",
            dimension: "item",
            as: "percent"
        });
        const cols = {
            percent: {
                formatter: val => {
                    val = Number(val * 100).toFixed(2) + "%";
                    return val;
                }
            }
        };
        return <div>
            <Chart
                height={380}
                data={dv}
                scale={cols}
                padding={[80, 100, 80, 80]}
                forceFit
            >
                <Coord type="theta" radius={0.75} />
                <Axis name="percent" />
                <Legend
                    position="bottom"
                />
                <Tooltip
                    showTitle={false}
                    itemTpl="<li><span style=&quot;background-color:{color};&quot; class=&quot;g2-tooltip-marker&quot;></span>{name}: {value}</li>"
                />
                <Geom
                    type="intervalStack"
                    position="percent"
                    color="item"
                    tooltip={[
                        "item*percent",
                        (item, percent) => {
                            percent = Number(percent * 100).toFixed(2) + "%";
                            return {
                                name: item,
                                value: percent
                            };
                        }
                    ]}
                    style={{
                        lineWidth: 1,
                        stroke: "#fff"
                    }}
                >
                    <Label
                        content="percent"
                        formatter={(val, item) => {
                            return item.point.item + ": " + val;
                        }}
                    />
                </Geom>
            </Chart>
        </div>
    }
}
export default PieChart;